    color: white;
    cursor: pointer;
    background: #5457b6;
  }
  .form1 {
    display: none;
  }
  .reply-form-mobile {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  /* Mycomments: Comments and Relies that are made by the user */
  .edit-options {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: flex-end;
    gap: 10px;
    width: 100%;
    height: 100%;
  }
  .header-btn {
    display: none !important;
  }
  .header-btn-mobile {
    display: flex !important;
    flex-flow: row wrap;
    background-color: transparent !important;
  }
}

